
html, body{
    margin: 0;
    padding:0;
    height: 100%;
    width: 100%;
}
hr{
    margin-top: 1rem;
    border: 0;
    border-top: 1px solid #eee;
}
div.bg-wrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    margin:0;
    padding: 0;
    background-color: #d2d6de;
}
div.logo{
    line-height: 1em !important;
    font-size: 35px;
    text-align: center;
    font-weight: 300;
    margin-bottom: 25px;
    color: #444;
}
div.box-content{
    background-color: #fff;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
}
@media(min-width:767px){
div.box{
        width:400px;
        margin: 7% auto;
        box-sizing: border-box;
}
}
@media(max-width:766px){
div.box{
        width:90%;
        margin-top:20px;
        margin-left:auto;
        margin-right: auto;
}
}



